/*
 |--------------------------------------------------------------------------
 | Load Application Dependencies
 |--------------------------------------------------------------------------
 |
 | First we will load all of this project's JavaScript dependencies which
 | includes Vue and other libraries. It is a great starting point when
 | building robust, powerful web applications using Vue and Laravel.
 |
 */
import 'es6-promise/auto';
import Vue from 'vue';

window.addEventListener('load', () => {
    const instance = document.querySelectorAll('.acf-image-annotation-field');

    window.Vue = Vue;
    window.eventBus = new Vue();
    // eslint-disable-next-line global-require
    require('./config/load-backend-components');

    if (_.isNull(instance) === false) {
        Array.prototype.forEach.call(instance, (el, i) => {
            const element = document.querySelector(`#${el.getAttribute('id')}`);
            if (_.isNull(element) === false) {
                const app = new Vue({
                    el: element,
                });
            }
        });
    }
});


