<template>
   <section class="acf-image-annotation">

       <div class="acf-input">
            <div class="acf-fields -top -border">
                <div class="acf-field acf-field-text acf-field-5aeace0a361ef" data-name="Image" data-type="text" data-key="">
                    <div class="acf-label">
                        <label>Image</label>
                        <span class="acf-icon -cancel dark" v-if="preview !== null" @click="reset"></span> 
                    </div>
                    <div class="acf-input">
                        <div class="acf-input-wrap">
                            <div class='image-preview-wrapper'>
                                <div id="image-preview" class="c-preview">
                                    <img :src="preview" id="preview-img" ref="imageItem" @click="getClickPosition">
                                    <div class="c-annotate" v-for="(annotate, index) in annotations" :style="styleItem(annotate)" :key="index" v-text="`${index + 1}`"></div>
                                </div>
                            </div>
                            <a class="acf-button button" v-if="showUploadButton" @click="openMediaFrame">Add Image</a>
                        </div>		
                    </div>
                </div>
            </div>
        </div>
        <!-- Lists -->
        <div class="acf-field acf-field-repeater">
            <br>
            <div class="acf-label">
                <label for="acf-field_5aeacdfb361ee-field_5aeace0f361f0">Annotations</label>
            </div>
            <div class="acf-input">
                <div class="acf-repeater -table" data-min="0" data-max="0">
                    <table class="acf-table">
                        <thead>
                            <tr>
                                <th class="acf-row-handle"></th>
                                <th class="acf-th" data-name="item" data-type="text" style="width: 100%;">Item</th>
                                <th class="acf-row-handle"></th>
                            </tr>
		                </thead>
                        <tbody v-if="annotations">
                            <tr class="acf-row" v-for="(item, index) in annotations" :key="index" data-id="">
                                <td class="acf-row-handle order" title="Drag to reorder">
                                    <span v-text="`${index + 1}`"></span>
                                </td>
                                <!-- -->
                                <td class="acf-field acf-field-text">
                                    <div class="acf-input">
                                        <div class="acf-label">
                                            <label>Title</label>
                                        </div>
                                        <div class="acf-input-wrap">
                                            <input type="text" name="itemName" v-model="item.title">
                                        </div>					
                                    </div>
                                    <br>
                                    <div class="acf-input">
                                        <div class="acf-label">
                                            <label>Summary</label>
                                        </div>
                                        <div class="acf-input-wrap">
                                            <textarea type="text" name="itemSummary" v-model="item.summary"></textarea>
                                        </div>					
                                    </div>
                                </td>
                                <!-- -->
                                <td class="acf-row-handle remove">
                                    <span class="acf-icon -minus small" @click="removeItem(index)"></span>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <input type="hidden" :name="fieldName" :value="dataToSave">
   </section>
</template>
<script>

export default {
    props: {
        postId: {
            type: [Number, String],
            default: 0
        },
        fieldName: {
            type: String,
            default: ''
        },
        fieldKey: {
            type: String,
            default: ''
        },
        fieldData: {
            type: [Array, Object, String],
            default() {
                return null 
            }
        }
    },
    mounted() {
        if (this.fieldData !== null) {
            const fieldData = JSON.parse(this.fieldData);
            this.preview = fieldData.image;
            this.height = fieldData.height;
            this.width = fieldData.width;
            this.annotations = fieldData.annotations;
            this.showUploadButton = (fieldData.image !== null) ? true : false;
        }
    },
    data() {
        return {
            mediaFrame: null,
            preview: null,
            showUploadButton: true,
            height: 0,
            width: 0,
            id: null,
            annotations: []
        }
    },
    computed: {
        styles: function() {
            return {
                backgroundImage: `url(${this.preview})`,
                width: `${this.width}px`,
                height: `${this.height}px`
            }
        },
        dataToSave: function() {
            const dataObj = {
                imageId: this.id,
                image: this.preview,
                height: this.height,
                width: this.width,
                annotations: this.annotations
            }
        
            return JSON.stringify(dataObj);
        }
    },
    methods: {
        openMediaFrame: function() {

            if (this.mediaFrame) {
                this.mediaFrame.uploader.uploader.param('post_id', parseInt(this.postId))
                // open frame
                this.mediaFrame.open();
                return;
            } else {
                // set the wp.media post id so the uploader grabs the ID we want when initialised
                wp.media.model.settings.post.id = parseInt(this.postId);
            }

            // create the media frame.
            this.mediaFrame = wp.media.frames.file_frame = wp.media({
                title: 'Select a image to upload',
                button: {
                    text: 'Use this image',
                },
                multiple: false
            });

            // when an image is selected, run a callback
            this.mediaFrame.on('select', () => {
                let attachment = this.reactiveJson(this.mediaFrame.state().get('selection').first());
                console.log(attachment);
                this.showUploadButton = false;
                this.id = attachment.id;
                this.preview = attachment.url;
                this.width = attachment.width;
                this.height = attachment.height;
            });

            // open the model
            this.mediaFrame.open();
        },
        getClickPosition: function(e) {

            const parentPosition = this.getPosition(e.currentTarget);
            const xPosition = e.clientX - parentPosition.x - (30 / 2);
            const yPosition = e.clientY - parentPosition.y - (30 / 2);
            const width = this.$refs.imageItem.clientWidth;
            const height = this.$refs.imageItem.clientHeight;

            // convert position to percentage values
            let x = this.roundUp((xPosition / width) * 100, 0);
            let y = this.roundUp((yPosition / height) * 100, 0);

            // push new annotations
            this.annotations.push({
                x: x,
                y: y,
                title: null,
                summary: null,
            });
        },
        getPosition: function(el) {
           let xPos = 0;
           let yPos = 0;

            while (el) {

                if (el.tagName == "BODY") {

                    // deal with browser quirks with body/window/document and page scroll
                    const xScroll = el.scrollLeft || document.documentElement.scrollLeft;
                    const yScroll = el.scrollTop || document.documentElement.scrollTop;
                
                    xPos += (el.offsetLeft - xScroll + el.clientLeft);
                    yPos += (el.offsetTop - yScroll + el.clientTop);

                } else {

                    // for all other non-BODY elements
                    xPos += (el.offsetLeft - el.scrollLeft + el.clientLeft);
                    yPos += (el.offsetTop - el.scrollTop + el.clientTop);
                }
            
                el = el.offsetParent;
            }

            return {
                x: xPos,
                y: yPos
            }
        },
        reactiveJson: function(dataObj) {
            return JSON.parse(JSON.stringify(dataObj));
        },
        roundUp: function(num, precision) {
            precision = Math.pow(10, precision);
            return Math.ceil(num * precision) / precision;
        },
        styleItem: function(coordinate) {
            return {
                top: `${coordinate.y}%`,
                left: `${coordinate.x}%`
            }
        },
        removeItem: function(index) {
            this.annotations.splice(index, 1);
        },
        itemName: function(index) {
            return `${this.fieldName}[${index}]`;
        },
        reset: function() {
            this.width = 0;
            this.height = 0;
            this.preview = null;
            this.annotations = [];
            this.showUploadButton = true;
        }
    }   
}
</script>

<style>
.c-preview {
    /* Box-model */
    max-width: 100%;

    /* Positioning */
    position: relative;

    /* Visual */
    /* background-size: contain;
    background-repeat: no-repeat; */

    /* Misc */
    cursor: crosshair;
}

.c-preview img {
    width: 100%;
    height: auto;
}

.c-annotate {
    /* Positioning */
    position: absolute;

    /* Visual */
    background-color: #182753;
    color: #ffffff;

    /* Box-model */
    height: 30px;
    width: 30px;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
    font-weight: 700;
}

.image-preview-wrapper {
    position: relative;
}

span.acf-icon.dark {
    position: absolute;
    top: 0;
    right: 0;
}

.image-preview-wrapper:hover span.acf-icon.dark {
    display: block;
}

span.acf-icon {
    /* Misc */
    cursor: pointer;
    color: #999;
    border-color: #BBB;
    background-color: #fff;
}

span.acf-icon:hover {
    /* Visual */
    color: #fff;
    border-color: transparent;
    background-color: #F55E4F;
}
</style>


